.group-info {
  display: none;
}

@media screen and (min-width: $breakpoint-tablet) {
  .group-info {
    display: flex;
    justify-content: center;
    height: 0;
    width: 100%;
    position: fixed;
    z-index: 1;
    bottom: 0;
    background-color: rgba(247, 179, 134, 0.9);
    overflow-x: hidden;
    transition: 0.5s;

    &__closebtn {
      position: absolute;
      top: 0;
      right: 25px;
      font-size: 36px;
      padding: 8px 8px 8px 32px;
      text-decoration: none;
      font-size: 25px;
      color: $main-text-color;
      display: block;
      transition: 0.3s;
      &:hover {
        color: $second-text-color;
      }
    }

    &__wrapper {
      display: flex;
      flex-direction: column;
      gap: 20px;
      align-items: center;
    }

    &__name {
      padding-top: 20px;
      color: $main-text-color;
      font-size: 24px;
      text-transform: uppercase;
      font-weight: 500;
    }
  }

  .team {
    max-width: 500px;
    display: flex;
    flex-wrap: wrap;
    row-gap: 45px;
    column-gap: 20px;
    align-items: center;
    justify-content: center;
  }

  .team__item {
    position: relative;
  }

  .team__member {
    font-size: 30px;
    width: 75px;
    height: 75px;
    overflow: hidden;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &:hover .team__name {
      opacity: 1;
    }

    &:active .team__name {
      opacity: 0.7;
    }
  }

  .team__overlay {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    a {
      text-decoration: none;
      color: #fff;
    }
  }

  .team__avatar {
    border-radius: 50%;
    width: 100%;
    height: 100%;
  }

  .team__name {
    color: #050505;
    font-size: 12px;
    position: absolute;
    white-space: pre;
    text-align: center;
    bottom: -30px;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
  }
}
