.loader {
  width: 60px;
  height: 60px;
  border: 4px solid #f2eaea76;
  border-top-color: #ff6b08;
  border-radius: 50%;
  margin-top: -20px;
  margin-left: -20px;
  animation: 3s linear infinite spin;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

